.bg-img {

  &__board {
    width: 100%;
    position: absolute;
    z-index: 3;
    bottom: -20%;
    left: 25%;

    img {
      width: 35%;
    }
  }

  &__coin {
    position: absolute;

    img {
      opacity: 0.5;
    }

    &--top {
      top: -15%;
      left: -5%;

      img {
        width: 230%;
      }
    }

    &--bottom {
      bottom: -19%;
      right: 3%;

      img {
        width: 210%;
      }
    }
  }
}
